.chart__inputs {
  display: flex;
  gap: 1.5em;
  flex-direction: column;
  margin-bottom: 10px;
}

.input {
  display: block;
  width: 100%;
  height: calc(2.25rem + 2px);
  padding: 0.375rem 0.75rem;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #bdbdbd;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

  &--error {
    border-color: red;
  }

  &__message-error {
    position: absolute;
    color: red;
    font-size: 0.8em;
  }
}
